.fun-plans {
  position: relative;

  @media (min-width: @tablet-width) {
    margin-right: 33px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 0;
    width: 73%;
  }

  & + &::after {
    content: "";
    position: absolute;
    bottom: 96.1%;
    left: -15px;
    width: 2px;
    height: 108.1%;
    background-color: @basic-blue-light;

    @media (min-width: @tablet-width) {
      left: auto;
      right: -27px;
      bottom: 95.5%;
      height: 122%;
    }

    @media (min-width: @desktop-width) {
      left: auto;
      right: -45px;
      bottom: 95%;
      height: 122%;
    }
  }

  &--error::before {
    content: "    ";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 20px 21px;
    box-sizing: border-box;
    border-radius: 0 0 4px 4px;
    .fonts(0; 4; 400);
    color: @error-color;
    background-color: @error-bg;

    @media (min-width: @tablet-width) {
      .fonts(4; 4; 400);
    }
  }

  &--error .fun-plans__plan {
    border-color: @border-error;
  }
}

.fun-plans__name {
  display: inline-block;
  width: 100%;
  .fonts(2; 2; 500);
  color: @basic-blue-light;
  margin-top: 2px;
  margin-bottom: 18px;

  @media (min-width: @tablet-width) {
    .fonts(14; 14; 700);
    margin-bottom: 38px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 2px;
    top: 10px;
    left: -15px;
    background-color: @basic-blue-light;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: -3px;
    right: 0;
    width: 70px;
    height: 25px;
    background: linear-gradient(to right, rgba(@basic-white, 0) 0%, @basic-white 20%, @basic-white 100%);
  }
}

.fun-plans__name-text {
  position: relative;
  display: inline-block;
  width: 100%;
  margin-left: 11px;

  @media (min-width: @tablet-width) {
    margin-left: 0;
  }

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: -14px;
    width: 6px;
    height: 6px;
    background-color: @basic-blue-light;
    border-radius: 50%;

    @media (min-width: @tablet-width) {
      width: 14px;
      height: 14px;
      left: auto;
      right: -33px;
    }

    @media (min-width: @desktop-width) {
      right: -51px;
    }
  }
}

.fun-plans__name-flag {
  position: absolute;
  z-index: 50;
  top: -2px;
  right: 0;
  width: 35px;
  height: 25px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border-radius: 4px;

  @media (min-width: @tablet-width) {
    top: -8px;
    width: 70px;
    height: 47px;
  }

  @media (min-width: @desktop-width) {
    top: -3px;
    left: auto;
    right: -155px;
  }

  &--sri-lanka {
    background-image: url("../img/desktop/sri-lanka-flag.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop/sri-lanka-flag@2x.png");
    }
  }

  &--france {
