    }
  }

  &--belgium {
    background-image: url("../img/desktop/belgium-flag.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop/belgium-flag@2x.png");
    }
  }

  &--thailand {
    background-image: url("../img/desktop/thailand-flag.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop/thailand-flag@2x.png");
    }
  }

  &--seychelles {
    background-image: url("../img/desktop/seychelles-flag.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop/seychelles-flag@2x.png");
    }
  }

  &--england {
