  &--germany {
    background-image: url("../img/desktop/germany-flag.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop/germany-flag@2x.png");
    }
  }

  &--bosnia-and-herzegovina {
    background-image: url("../img/desktop/bosnia-and-herzegovina-flag.png");

    @media (min-resolution: 2dppx) {
