  }

  &--dominica {
    background-image: url("../img/desktop/dominica-flag.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/desktop/dominica-flag@2x.png");
    }
  }
}

.fun-plans__plan {
  border: 1px solid @special-border-grey;
  background-color: transparent;
  border-radius: 4px;
  resize: none;
  width: 100%;
  height: 220px;
  box-sizing: border-box;
  padding: 7px 12px;
  vertical-align: top;
  .set-font;
  .fonts(0; 7; 400);
  color: @special-grey-dark;
  position: relative;
  z-index: 50;

  &:hover {
    border-color: @counter-border-hover;
  }

  &:focus {
    border-color: @basic-black;
    outline: none;
  }

  @media (min-width: @tablet-width) {
    height: 142px;
    padding: 18px 115px 18px 19px;
    .fonts(4; 4; 400);
  }

  @media (min-width: @desktop-width) {
    height: 142px;
    padding: 26px 5px 26px 25px;
  }

  &::placeholder {
    color: rgb(@special-grey-dark, 0.3);
  }
}
