.alphabet {
  .list-style-none;
  box-sizing: border-box;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  position: relative;

  @media (min-width: @tablet-width) {
    width: 48.8%;
  }

  @media (min-width: @desktop-width) {
    column-gap: 16px;
    row-gap: 15px;
    width: 51%;
  }

  &--sort {
    padding: 0 25px 0 25px;

    @media (min-width: @tablet-width) {
      padding: 0;
      width: 87%;
      column-gap: 20px;
      row-gap: 25px;
    }

    @media (min-width: @desktop-width) {
      display: none;
    }
  }
}

.alphabet__item {
  text-align: center;
  box-sizing: content-box;
  background-color: @basic-white;
  border: 1px solid @special-border-grey;
  border-right-color: transparent;
  .fonts(-2; 2; 500);
  margin-top: -1px;
  margin-left: -1px;

  &:not(&--sort):hover,
  &:not(&--sort):focus {
    position: relative;
    border: 1px solid @counter-border-hover;
    z-index: 2;
  }

  &:not(&--sort):active {
    background-color: @basic-blue-light;
    border: 1px solid @basic-blue-light;
  }

  @media (min-width: @tablet-width) {
    .fonts(4; 4; 500);
  }

  @media (min-width: @desktop-width) {
    margin: 0;
    border: 1px solid @special-border-grey;
    border-radius: 4px;
  }

  &:nth-child(5n),
  &:last-of-type {
    margin-right: -1px;
    border-right: 1px solid @special-border-grey;

    &:not(&--sort):hover,
    &:not(&--sort):focus {
      position: relative;
      border-right: 1px solid @counter-border-hover;
      z-index: 2;
    }

    @media (min-width: @tablet-width) {
      margin-right: 0;
    }
  }

  &--current {
    background-color: @basic-grey;
    border-bottom: 0;

    @media (min-width: @desktop-width) {
      border-bottom: 1px solid @special-border-grey;
    }
  }

  &--sort {
    @media (max-width: @mobile-only-width) {
      &:hover,
      &:focus {
        position: relative;
        border: 1px solid @counter-border-hover;
        z-index: 2;
      }

      &:active {
        background-color: @basic-blue-light;
      }
    }

    @media (min-width: @tablet-width) {
      border: none;
      background-color: transparent;
    }
  }

  &--sort:nth-child(5n) {
    @media (min-width: @tablet-width) {
      border: none;
    }
  }

  &--current-sort {
    position: relative;
    z-index: 2;
    background-color: @basic-blue-light;
    border: 1px solid @basic-blue-light;

    @media (min-width: @tablet-width) {
      border: none;
      background-color: transparent;
    }
  }
}

.alphabet__button {
  .set-font;
  .fonts(-2; 2; 500);
  background-color: transparent;
  border: none;
  padding: 11px 0;
  width: 100%;
  color: @basic-blue-light;
  cursor: pointer;

  &:active {
    color: @basic-white;
  }

  @media (min-width: @tablet-width) {
    padding: 24px 0;
    .fonts(4; 4; 500);
  }

  &--sort {
    @media (min-width: @tablet-width) {
      opacity: 0.3;
      padding: 0;
      .fonts(44; 44; 700);

      &:hover,
      &:focus {
        opacity: 0.6;
      }

      &:active {
        color: @basic-blue-light;
        opacity: 1;
      }
    }
  }

  &--current-sort {
    color: @basic-white;

    @media (min-width: @tablet-width) {
      color: @basic-blue-light;
      opacity: 1;
    }
  }
}
